@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.confirmation-modal-wrapper {
  padding: 40px 24px;

  .confirm-btn {
    background-color: pick-color('secondary', 'red');

    &:hover {
      background-color: color.adjust(pick-color('secondary', 'red'), $lightness: -5%);
    }
  }

  .decline-btn {
    background-color: pick-color('common', 'dark');

    &:hover {
      background-color: color.adjust(pick-color('common', 'dark'), $lightness: -5%);
    }
  }

  .redact-file {
    background-color: color.adjust(pick-color('secondary', 'blue'), $lightness: 38%);
    padding: 6px 12px;
    border-radius: 4px;
    color: pick-color('secondary', 'blue');
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 600;

    .file-type-icon {
      @include circles(28px, pick-color('primary', 'web'));
      flex-shrink: 0;
    }
  }
}
